.why-apedin {
    vertical-align: middle;
    background: #ECF0F1;
}

.why-apedin-main {
    background: transparent;
}

.why-section i {
    font-size: 40px;
}

.why-section span {
    background: #fff;
    padding: 0.85em;
    display: inline-block;
    border-radius: 50%;
    width: 65px;
    height: 65px;
}

.why-section .title {
    margin-top: 0.75em;
    margin-bottom: 0.25rem;
    font-size: 16px;
    font-weight: 600;
}

.why-section .sub-title {
    font-size: 12px;
    line-height: 1;
}

.why-section img {
    float: left;
}

.drawer-icon h4 {
    font-size: 1.5rem;
    padding: 0.5em 0.2em;
    margin-top: 25%;
    transform: translateY(-25%);
}

.why-arrow {
    margin-top: 6px;
    position: absolute;
    margin-left: 5px;
    font-size: 24px !important;
}
