.category-card img {
    width: 180px;
    transition: 0.15s ease-in all;
}

.drawer {
    text-align: left;
}

.drawer .profile-pic {
    text-align: center;
}

.drawer .profile-pic img {
    box-shadow: 0px 3px 8px 0px rgba(32,32,32,0.35);
}

.drawer .profile-nav {
    background: #ccc;
}

.drawer .nav-link {
    color: #f00;
    transition: 0.15s ease-in all;
}

.drawer .nav-link:hover {
    color: #ffc107;
}

.category-card img:hover {
    transform: translateY(3px);
}

.category-section-title {
    text-align: left;
}