.Topbar {
    background-color: #f7cb00  !important;
}

.Header {
    background-color: #FFD200 !important;
}

.logo {
    padding: 0.25em 0em 1.5em 0em;
    text-align: left;
}

.search-shopping {
    margin-top: 3.5em;
}

.search-box {
    width: 88% !important;
    margin-right: 0 !important;
}

.search-button {
    width: 10%;
    margin: 0;
    background: red;
    border: red;
    position: absolute;
    right: 30px;
    height: 38px;
}

.search-button i {
    font-size: 40px !important;
    position: absolute;
    top: 0;
    left: 7px;
}

.Topbar a {
    font-size: 14px;
    vertical-align: middle;
    display: inline-flex;
    background: transparent;
    border: none;
    text-decoration: none;
    color: #000;
    margin-right: 10px;
}

.Topbar a i {
    font-size: 22px;
    color: #000;
}

.shopping-section {
    text-align: left;
}

.shopping-section i {
    color: #000;
    margin-right: 5px;
}

.shopping-section Button {
    font-weight: 400;
    color: #000;
    padding: 0em 0.75em;
    display: inline-flex;
    vertical-align: middle;
    height: 38px;
    margin-right: 5px;
    overflow: hidden;
}